
import LoadMore from 'vux/src/components/load-more/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'

import Scroll from './pulldown'

export default {
components: {
LoadMore,
Cell,
Group,
Badge,
Divider,
Flexbox,
FlexboxItem,
Datetime,
CellFormPreview,
'v-scroll': Scroll
},
data () {
return {
invest_date: '',
received_date: '',
value1: '2015-11-12',
index: 0,
loadmore: false,
loadbottom: false,
orderList: [],
loadingText: '',
pageNum: 1, // 当前页面
pageSize: 10,  // 一次显示多少条
listdata: [], // 下拉更新数据存放数组
downdata: []  // 上拉更多的数据存放数组
}
},
created: function () {
this.fetchData(this.pageNum, this.pageSize)
},
watch: {

'$route': 'fetchData'
},
methods: {
dateChange: function () {
this.loadbottom = false
this.orderList = []
this.pageNum = 1
this.fetchData(1, 10)
},
fetchData: function (pageNum, pageSize) {
var that = this
this.loadmore = true
var params = 'pageNum=' + pageNum + '&pageSize=' + pageSize + '&invest_date=' + this.invest_date + '&received_date=' + this.received_date
this.noLoadPost('/api/auth/order/order_list', params, function (response) {
that.orderList = response.data
that.loadmore = false
})
},
onRefresh (done) {
this.loadbottom = false
this.orderList = []
this.pageNum = 1
this.invest_date = ''
this.received_date = ''
this.fetchData(1, 10)
done()
},
onInfinite (done) {
var that = this
if (this.loadbottom === true) {
done()
return
} else {
this.loadmore = true
this.pageNum++
let pageNum = this.pageNum
var params = 'pageNum=' + pageNum + '&pageSize=' + this.pageSize
this.noLoadPost('/api/auth/order/order_list', params, function (response) {
that.downdata = response.data
if (that.downdata.length === 0) {
that.loadbottom = true
} else {
that.orderList = that.orderList.concat(that.downdata)
}
that.loadmore = false
done()
})
}
}
},
computed: {
filterOrderList () {
var orderList = this.orderList
var that = this
return orderList.filter(function (item) {
item.list = []
item.list.push({label: '投资人', value: item.investor_phone + ' - ' + item.investor_name})
item.list.push({label: '投资日期', value: item.invest_date})
item.list.push({label: '回款日期', value: item.received_date})
item.cashBack = that.formatterCurrency(item.cash_back, 2, '￥')
item.interest_m = that.formatterCurrency(item.interest, 2, '￥')
item.list.push({label: '返现/利息', value: item.cashBack + '/≈' + item.interest_m})
return true
})
}
}
}



